export function getSummaryMsg(position: number, isOurTournament = false) {
    let title: string, msg: string
    if (isOurTournament){
        switch (position){
            case 1:
                title = " !!";
                msg =`
                       \n
                   ! \n
                          ". \n
                  ,    
                `;
                break;
            case 2:
                title = "  !";
                msg =`
                 ! ( ) \n
                   ! \n
                      \n
                          2,    
                `;
                break;
            case 3:
                title = " !";
                msg =`
                    ...  \n
                       ,  '   ! \n
                \n
                   ,  !
                `;
                break;
            case 4:
                title = "Break Even";
                msg =`
                 ,         \n
                        \n
                  ,    ,    !
                `;
                break;
            case 5:
                title = "  ";
                msg =`
                        ,\n
                      \n
                    ...\n
                    ,    !
                `;
                break;
            case 6:
            case 7:
                title = "  ";
                msg =`
                 ,        ... \n\n
                 : \n
                    ! \n
                       ... \n
                 
                `;
                break;
            case 8:
            case 9:
                title = "   ";
                msg = `  , \n
                  , \n
                ... \n
                      \n
                  ,    !
                `;
                break;
            case 10:
            case 11:
            case 12:
            case 13:
                title = "  ";
                msg = `    ,   ... \n
                   ,           \n\n
                    
                `;
                break;
            case 14:
                title = "    ";
                msg = `   , \n
                      ... \n
                    !
                
                \n
                       ,    `;
                break;
            case 15:
                title = "  ";
                msg = ` , \n
                      ,   !
                \n
                   ...`;
                break;
            default:
                title = "you are not the winner";
                msg = "cheer up mate";
        }
    } else {
        switch (position){
            case 1:
                title = "!!";
                msg =`
                  !  \n
                      ,     . \n
                  
                `;
                break;
            default:
                title = "You are not the winner";
                msg = "Cheer up, mate";
        }
    }
    return {title, msg};
}